:import('Styles/palette.css') {
  i__const_darkColor_57: darkColor;
  i__const_primaryColor_58: primaryColor;
  i__const_neutralColorDark_59: neutralColorDark;
  i__const_neutralColorMedium_60: neutralColorMedium;
  i__const_whiteColor_61: whiteColor;
  i__const_pinkRed_62: pinkRed;
}

:export {
  colors: 'Styles/palette.css';
  darkColor: i__const_darkColor_57;
  primaryColor: i__const_primaryColor_58;
  neutralColorDark: i__const_neutralColorDark_59;
  neutralColorMedium: i__const_neutralColorMedium_60;
  whiteColor: i__const_whiteColor_61;
  pinkRed: i__const_pinkRed_62;
}

.title, .subtitle, .header {
  margin: 0;
}

.p {
  color: i__const_darkColor_57;
}

.label {
  opacity: 0.5;
  font-size: 14px;
  margin-bottom: 2px;
}

.highlight {
  color: i__const_primaryColor_58;
}

.secondary {
  color: i__const_neutralColorDark_59;
}

.disabled {
  color: i__const_neutralColorMedium_60;
  cursor: not-allowed;
}

.inverted {
  color: i__const_whiteColor_61;
}

.smallText {
  font-size: 13px;
}

.lead {
  font-size: 18px;
}

.bolder {
  font-weight: bold;
}

.lighter {
  font-weight: 400;
}

.error {
  color: i__const_pinkRed_62;
}

.fontFamily{
  font-family: 'open-sans';
}



