:import('Styles/palette.css') {
  i__const_clearBlue_36: clearBlue;
  i__const_purplishBlue_37: purplishBlue;
  i__const_pinkRed_38: pinkRed;
  i__const_dark_39: dark;
  i__const_whiteColor_40: whiteColor;
  i__const_neutralColorLight_41: neutralColorLight;
  i__const_neutralColorMedium_42: neutralColorMedium;
}

 :export {
  clearBlue: i__const_clearBlue_36;
  purplishBlue: i__const_purplishBlue_37;
  pinkRed: i__const_pinkRed_38;
  dark: i__const_dark_39;
  whiteColor: i__const_whiteColor_40;
  neutralColorLight: i__const_neutralColorLight_41;
  neutralColorMedium: i__const_neutralColorMedium_42;
  cardShadow: 0px 1px 2px rgba(0, 0, 0, .5);
  leaveModalTransition: 200ms;
  navbarHeight: 49.6px;
  navbarHeight: 3.1rem;
  headerHeight: 70.4px;
  headerHeight: 4.4rem;
}

