   max-width: 500px;
 }

 .btnPrimary {
   background-color: i__const_purplishBlue_37;
 }

 .btnPrimary:hover{
   opacity: 0.8;
 }

 .btnSecondary {
   background-color: i__const_pinkRed_38;
 }

 .btnSecondary:hover{
   opacity: 0.8;
 }

 :global(.nav) {
   text-decoration: none;
   color: i__const_dark_39;
   padding: 0 10;
   font-weight: bold;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
 }

 :global(.leaveModal) {
   opacity: 0.01;
   -webkit-transition: 200ms;
   transition: 200ms;
 }

 :global(.active-nav) {
   color: i__const_clearBlue_36;
   border-bottom: 3px solid;
 }



